
.login{margin-top: 2.5rem;
}
.login .top{width: 100%;height: 3.77rem;background: #51bf63;position: relative;
}
.login .top .pic img{width: 2.1rem;height: 2.1rem;position: absolute;top: .8rem;left:.6rem
}
.login .logWrap{position: absolute;height: 1.15rem;width: 5.3rem;border: .01rem solid #fff;line-height: 1.15rem;text-align: center;color: #fff;font-size: .5rem;border-radius: .05rem;top:1.3rem;right: 2.15rem
}
.login .logWrap a{text-decoration: none;color: #fff;font-size: .5rem;
}
.login .midd{height: 1.6rem;width: 11.82rem; border-bottom: .01rem solid #dfdfdf;margin-left: .6rem;line-height: 1.6rem;margin-top: .45rem
}
.login .midd img{width: .72rem;height: .72rem;float: left;padding-top: .4rem
}
.login .midd span{font-size: .5rem;
}
.login .remind{color: #494949;float: left;padding-left: .4rem
}
.login .right{float: right;padding-right: .7rem
}
.login .bot{height: 1.40rem;color: #ccc;font-size: .5rem;line-height: 1.4rem;text-align: center;margin-bottom: .45rem
}



