
.filmWrap ul{width: 100%;height: 1.20rem;border-bottom: 0.01rem solid #e4e4e4;padding-bottom: 0.03rem;margin-top: 2.5rem;text-align: center;
}
.filmWrap li{float: left;width: 20%;list-style: none;height: 1.20rem;
}
.filmWrap li a{text-decoration: none;font-size: 0.44rem;color: #9a9a9a;display: block;line-height: 1.20rem;
}
.filmWrap .active{border-bottom: 0.03rem solid #63c882;
}
.filmWrap .active a{color:#63c882;
}



